body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#whole-app {
  height: 100vh;
  width: 100vw;
  background-color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#Header {
  height: 10%;
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: top;
          align-items: top;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 80%;
  padding-bottom: 5%;
}

#Main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: top;
          align-items: top;
  height: 50%;
  width: 100%;
}

h1, h3, #break-length, #session-length {
  color: seashell;
}

.Break-Sesh {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: top;
          align-items: top;
  text-align: center;
  height: 100%;
  width: 80%;
  padding-left: 2.5%;
}

.incDec {
  background-color: yellow;
  font-size: 100%;
  font-weight: bold;
  width: 25%;
}

.flex-third {
  height: 100%;
  width: 30%;
  border-radius: 5px;
  background-color: gray;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#timer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-bottom: 2.5%;
}

#timer-label {
  height: .75%;
}

#time-left {
  width: 80%;
  text-align: center;
  line-height: 200%;
  background-color: darkslateblue;
  color: limegreen;
  border-radius: 5px;
}

#time-left p {
  font-size: 100%;
  width: 100%;
}

.timer-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: top;
          align-items: top;
  color: whitesmoke;
  width: 80%;
  height: 25%;
  font-size: 100%;
}

#start_stop {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-image: linear-gradient(to right,rgb(43, 197, 43),rgb(196, 196, 34));
  border-radius: 5px;
  height: 50%;
  width: 40%;
  padding: 2%;
}
#reset {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgb(240, 4, 51);
  border-radius: 5px;
  height: 50%;
  width: 40%;
  padding: 2%;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 100%;
  width: 10%;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
}

