#whole-app {
  height: 100vh;
  width: 100vw;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#Header {
  height: 10%;
  width: 50%;
  display: flex;
  align-items: top;
  justify-content: center;
  font-size: 80%;
  padding-bottom: 5%;
}

#Main {
  display: flex;
  justify-content: center;
  align-items: top;
  height: 50%;
  width: 100%;
}

h1, h3, #break-length, #session-length {
  color: seashell;
}

.Break-Sesh {
  display: flex;
  justify-content: space-evenly;
  align-items: top;
  text-align: center;
  height: 100%;
  width: 80%;
  padding-left: 2.5%;
}

.incDec {
  background-color: yellow;
  font-size: 100%;
  font-weight: bold;
  width: 25%;
}

.flex-third {
  height: 100%;
  width: 30%;
  border-radius: 5px;
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#timer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 2.5%;
}

#timer-label {
  height: .75%;
}

#time-left {
  width: 80%;
  text-align: center;
  line-height: 200%;
  background-color: darkslateblue;
  color: limegreen;
  border-radius: 5px;
}

#time-left p {
  font-size: 100%;
  width: 100%;
}

.timer-buttons {
  display: flex;
  justify-content: space-between;
  align-items: top;
  color: whitesmoke;
  width: 80%;
  height: 25%;
  font-size: 100%;
}

#start_stop {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to right,rgb(43, 197, 43),rgb(196, 196, 34));
  border-radius: 5px;
  height: 50%;
  width: 40%;
  padding: 2%;
}
#reset {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(240, 4, 51);
  border-radius: 5px;
  height: 50%;
  width: 40%;
  padding: 2%;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 100%;
  width: 10%;
  pointer-events: none;
}

@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg)
  }
}
